
import MButton from '../../../base/button/button'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import { mapGetters } from 'vuex'
export default {
name: 'exChangeCoin',
components: {
MButton,
XInput,
Group,
Cell
},
computed: {
maxValue: {
get () {
return this.userCoin.availableGold
},
set (val) {
return val
}
},
...mapGetters([
'userInfo',
'userCoin'
])
},
data () {
return {
isDisabled: true,
coinNum: null
}
},
watch: {
coinNum (val) {
if (val.length > 0 && val <= this.maxValue) {
this.isDisabled = false
return false
}
this.isDisabled = true
}
},
methods: {
sureExChange () {

this.$axios.post('/api/withdrawApply/buyer/insertWithdrawApply', {
withdrawType: 1,
buyerUserId: this.userInfo.buyerUserId,
deposit: this.coinNum,
telephone: this.userInfo.telephone,
bankNo: this.userInfo.bankCardNo,
bankName: this.userInfo.bankName,
fullName: this.userInfo.fullName
}).then((response) => {
let data = response.data
if (data.code !== '200') {
this.$vux.alert.show({
title: '',
content: data.message
})
} else {
this.$vux.toast.show({
text: 'Loading',
position: 'middle',
onHide: () => {
this.$router.push({ name: 'user' })
}
})
}
}).catch((error) => {
console.log(error)
})
}
}
}



